#section_allergy {
  padding: 20px;
}

.section-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.scroll-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.scroll-button {
  background-color: #007BFF;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
}

.scroll-button:hover {
  background-color: #0056b3;
}

#restaurant-wrap {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
}

.restaurant-container {
  flex: 0 0 auto;
  width: 200px;
  margin-right: 20px;
}

.restaurant-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.restaurant-info {
  margin-top: 10px;
}

.restaurant-name {
  font-weight: bold;
}

.rating-stars {
  position: relative;
  display: inline-block;
  font-size: 18px;
  line-height: 1;
}

.filled-stars {
  color: gold;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.restaurant-area {
  font-size: 14px;
  color: #555;
}

.rest-rate {
  font-size: 16px;
  font-weight: bold;
  color: #333;
}
